:root {
  --primary: rgb(0, 139, 182);
  --secundary: rgb(255, 187, 0);
  --dark: rgb(49, 49, 49);
  --light: rgb(156, 156, 156);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  background-color:  var(--light);
}

.App {
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  padding: 16px;
}

.calculator {
  width: 100%;
  max-width: 400px;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.2);
}
.display {
  padding: 16px;
  text-align: right;
  background-color: var(--dark);
  color: var(--light);
  font-size: 24px;
  font-weight: 300;
}
.display span {
  font-size: 14px;
  color: rgb(95, 95, 95);
}
button {
  appearance: none;
  border: none;
  outline: none;
  color: rgb(255, 255, 255);
  font-size: 20px;
  padding: 16px;
  cursor: pointer;
  transition: 0.5s;
}
button:hover {
  opacity:0.8;
}
.operators {
  display: flex;
}
.operators button {
  flex: 1 1 0%;
  background-color: var(--primary);
  font-weight: 700;
}
.digits {
  display: flex;
  flex-wrap: wrap;
}
.digits button {
  flex: 1 1 33.333%;
  max-width: 33.333%;
  background-color: var(--dark);
}
